
import React from 'react'
import './SwatchEditor.css'
import _ from 'lodash'
import StyleEditor from '../StyleEditor'
import util from '../../services/util'

class SwatchEditor extends React.Component {

  constructor(props) {
    super(props)

    this.state = {
      open: true
    }
  }

  updateStyle(style) {
    let styles = _.cloneDeep(this.props.component.styles)
    let index = _.findIndex(styles, {id: style.id})

    styles[index] = style

    this.props.update({styles: styles})
  }

  updateTitle(title) {
    let swatch = this.props.swatch
    let swatches = _.cloneDeep(this.props.component.swatches)
    let index = _.findIndex(swatches, {id: swatch.id})

    swatch.title = title

    swatches[index] = swatch

    this.props.update({swatches: swatches})
  }

  addStyle() {
    let style = {
      id: util.hashString(),
      propInput: '',
      swatch_id: this.props.swatch.id,
      domKey: '',
      domValue: ''
    }

    let styles = _.cloneDeep(this.props.component.styles)

    styles.push(style)

    this.props.update({styles: styles})
  }

  removeStyle(id) {
    let styles = _.filter(this.props.component.styles, (style) => {
      return style.id !== id
    })

    this.props.update({styles: styles})
  }

  dragStart(swatch) {
    let swatches = _.cloneDeep(this.props.component.swatches)
    _.find(swatches, {id: swatch.id}).dragged = true

    this.props.update({swatches: swatches})
  }

  dragEnd(swatch) {
    let targetElement = _.find(this.props.component.elements, {targeted: true})

    let swatches = _.map(this.props.component.swatches, (s) => {
      if (s.dragged) {
        s.element_id = targetElement ? targetElement.id : s.element_id
      }

      s.dragged = false
      return s
    })

    let elements = _.map(this.props.component.elements, (element) => {
      element.targeted = false
      return element
    })

    this.props.update({
      swatches: swatches,
      elements: elements
    })
  }

  render() {
    let swatch = this.props.swatch
    let styles = _.filter(this.props.component.styles, {swatch_id: swatch.id})

    return (
      <div
        className={`SwatchEditor ${this.state.open ? 'open' : ''}`}
        draggable={true}
        onDragStart={() => this.dragStart(swatch)}
        onDragEnd={() => this.dragEnd(swatch)}>

        <div className="swatch-content-wrapper">
          <div className="swatch-header">
            <input className="swatch-title" type="text" value={swatch.title} onChange={(e) => this.updateTitle(e.target.value)} />
            <i className={`fa fa-${this.state.open ? 'minus' : 'plus'} swatch-header-icon toggle`} onClick={() => this.setState({open: !this.state.open})}></i>
            <i className="fa fa-times swatch-header-icon remove" onClick={() => this.props.remove(swatch.id)}></i>
          </div>

          <div className="style-list">
            {styles.map((style, i) => {
              return (
                <StyleEditor index={i} remove={(id) => this.removeStyle(id)} update={(style) => this.updateStyle(style)} key={style.id} style={style}></StyleEditor>
              )
            })}
            <button className="add-style" onClick={() => this.addStyle()}>
              add style
            </button>
          </div>
        </div>
      </div>
    )
  }

}

export default SwatchEditor
