
import React from 'react'
import './EditingCanvas.css'
import Mock from '../Mock'
import OrientationGraphic from '../OrientationGraphic'
import OrientationIcon from '../OrientationIcon'
import Anchor from '../Anchor'
import Guideline from '../Guideline'
import event from '../../services/event'

class EditingCanvas extends React.Component {

  constructor(props) {
    super(props)

    this.canvasRef = React.createRef()

    this.dragEventId = event.on('appDrag', (params) => {
      this.dragCanvas(params.rect)
    })

    this.endDragEventId = event.on('appDragEnd', (params) => {
      this.setState({isDragging: false, draggingAxis: null})
    })

    this.state = {
      showHandle: false,
      isDragging: false,
      draggingAxis: null,
      xGuideline: 50,
      yGuideline: 50,
      dir: null,
      showOverlay: false,
      highlightRef: null
    }
  }

  componentWillUnmount() {
    event.off('appDrag', this.dragEventId)
  }

  beginDrag(axis) {
    this.setState({
      draggingAxis: axis,
      isDragging: true
    })

    event.trigger('beginDrag')
  }

  dragCanvas(mouseCoords) {
    if (!this.canvasRef.current) {
      return
    }

    let canvasRect = this.canvasRef.current.getBoundingClientRect()

    if (this.state.draggingAxis === 'x') {
      let xPct = 0
      let canvasX = mouseCoords.x - canvasRect.x
      if (canvasX >= 0) {
        xPct = (canvasX / canvasRect.width) * 100
      }

      return this.setState({xGuideline: Math.min(xPct, 100)})
    }

    if (this.state.draggingAxis === 'y') {
      let yPct = 0
      let canvasY = mouseCoords.y - canvasRect.y
      if (canvasY >= 0) {
        yPct = (canvasY / canvasRect.height) * 100
      }

      return this.setState({yGuideline: Math.min(yPct, 100)})
    }
  }

  toggleOverlay(e, bool) {
    e.stopPropagation()

    let b = bool === undefined ? !this.state.showOverlay : !!bool

    this.setState({showOverlay: b})
  }

  selectDir(dir) {
    this.setState({
      dir: dir
    })
  }

  componentDidUpdate() {
    if (this.props.highlightId && this.state.highlightRef) {
      return
    } else if (!this.props.highlightId && !this.state.highlightRef) {
      return
    }

    this.setState({
      highlightRef: document.querySelector(`#el-${this.props.highlightId}`) || null
    })
  }

  renderHighlight(ref) {
    if (!ref) {
      return null
    }

    let rect = ref.getBoundingClientRect()

    let borderBoxStyle = {
      borderTopWidth: ref.style.borderTopWidth,
      borderRightWidth: ref.style.borderRightWidth,
      borderBottomWidth: ref.style.borderBottomWidth,
      borderLeftWidth: ref.style.borderLeftWidth,
      width: `${rect.width}px`,
      height: `${rect.height}px`,
      top: `${rect.top}px`,
      left: `${rect.left}px`
    }

    let marginBoxStyle = {
      top: `-${ ref.style.marginTop || '0px' }`,
      right: `-${ ref.style.marginRight || '0px' }`,
      bottom: `-${ ref.style.marginBottom || '0px' }`,
      left: `-${ ref.style.marginLeft || '0px' }`
    }

    let contentBoxStyle = {
      paddingTop: ref.style.paddingTop,
      paddingRight: ref.style.paddingRight,
      paddingBottom: ref.style.paddingBottom,
      paddingLeft: ref.style.paddingLeft
    }

    return (
      <div className="highlighter" style={borderBoxStyle}>
        <div className="highlight-margin-box" style={marginBoxStyle}></div>
        <div className="highlight-padding-box"></div>
        <div className="highlight-content-box" style={contentBoxStyle}></div>
      </div>
    )
  }

  render() {
    return (
      <div
        onMouseEnter={() => this.setState({showHandle: true})}
        onMouseLeave={() => this.setState({showHandle: false})}
        ref={this.canvasRef}
        className={`EditingCanvas ${this.state.showOverlay ? 'show-overlay' : ''}`}>
        <Anchor orientation={this.state.dir} x={this.state.xGuideline} y={this.state.yGuideline}>
          <Mock
            components={this.props.components}
            component={this.props.component}>
          </Mock>
        </Anchor>

        {this.renderHighlight(this.state.highlightRef)}

        <Guideline
          showHandle={this.state.showHandle && !this.state.showOverlay}
          showLine={this.state.draggingAxis === 'x' || this.state.showOverlay}
          pct={this.state.xGuideline}
          axis="x"
          beginDrag={() => this.beginDrag('x')}
          onReset={() => this.setState({xGuideline: 50})}>
        </Guideline>

        <Guideline
          showHandle={this.state.showHandle && !this.state.showOverlay}
          showLine={this.state.draggingAxis === 'y' || this.state.showOverlay}
          pct={this.state.yGuideline}
          axis="y"
          beginDrag={() => this.beginDrag('y')}
          onReset={() => this.setState({yGuideline: 50})}>
        </Guideline>
        <OrientationIcon clicked={(e) => this.toggleOverlay(e)}></OrientationIcon>
        <OrientationGraphic x={this.state.xGuideline} y={this.state.yGuideline} close={(e) => this.toggleOverlay(e, false)} select={(dir) => this.selectDir(dir)}></OrientationGraphic>
      </div>
    )
  }

}

export default EditingCanvas
