
import React from 'react'
import './Anchor.css'

export default (props) => {
  let orientation = props.orientation || 'center'
  let x = parseFloat(props.x)
  let y = parseFloat(props.y)

  let anchorStyle = {}
  let pointStyle = {}

  switch (props.orientation) {
    case 'north':
      anchorStyle.bottom = `${100 - y}%`
      pointStyle.left = `${x}%`
      break
    case 'northEast':
      anchorStyle.bottom = `${100 - y}%`
      anchorStyle.left = `${x}%`
      break
    case 'east':
      pointStyle.top = `${y}%`
      anchorStyle.left = `${x}%`
      break
    case 'southEast':
      anchorStyle.top = `${y}%`
      anchorStyle.left = `${x}%`
      break
    case 'south':
      anchorStyle.top = `${y}%`
      pointStyle.left = `${x}%`
      break
    case 'southWest':
      anchorStyle.top = `${y}%`
      anchorStyle.right = `${100 - x}%`
      break
    case 'west':
      pointStyle.top = `${y}%`
      anchorStyle.right = `${100 - x}%`
      break
    case 'northWest':
      anchorStyle.bottom = `${100 - y}%`
      anchorStyle.right = `${100 - x}%`
      break
    default:
      pointStyle.top = `${y}%`
      pointStyle.left = `${x}%`
      break
  }

  return (
    <div className={`Anchor ${orientation}`} style={anchorStyle}>
      <div className="anchor-point" style={pointStyle}>
        {props.children}
      </div>
    </div>
  )
}
