
import React from 'react'
import './Preview.css'
import _ from 'lodash'
import Canvas from '../Canvas'
import event from '../../services/event'
import util from '../../services/util'

class Preview extends React.Component {
  constructor(props) {
    super(props)

    this.listenerId = util.hashString()

    let component = _.cloneDeep(props.component)
    component.contexts = _.map(component.contexts, (ctx) => {
      ctx.selected = false
      return ctx
    })

    this.state = {
      openId: null,
      component: component,
      contexts: {}
    }

    event.on('appClick', () => {
      this.setState({openId: null})
    }, this.listenerId)
  }

  componentWillUnmount() {
    event.off('appClick', this.listenerId)
  }

  toggle(e, dimension_id) {
    e.stopPropagation()
    this.setState({openId: this.state.openId === dimension_id ? null : dimension_id})
  }

  selectContext(e, dimId, ctxId) {
    e.stopPropagation()

    this.setState((prevState) => {
      let contexts = _.cloneDeep(prevState.contexts)

      if (contexts[dimId] === ctxId) {
        return null
      }

      contexts[dimId] = ctxId

      let component = _.cloneDeep(prevState.component)

      let ctx_ids = _.values(contexts)

      component.contexts = _.map(component.contexts, (ctx) => {
        ctx.selected = ctx_ids.indexOf(ctx.id) > -1
        return ctx
      })

      return {
        component: component,
        openId: null,
        contexts: contexts
      }
    })
  }

  select(id) {
    this.setState((prevState) => {
      let states = _.map(prevState.component.states, (state) => {
        state.selected = state.id === id
        return state
      })

      let component = prevState.component
      component.states = states

      return {
        component: component
      }
    })
  }

  forward() {
    this.setState((prevState) => {
      let component = prevState.component
      let index = _.findIndex(component.states, {selected: true})

      let newIndex = index === -1 ? 0 : (index + 1)

      if (newIndex >= (component.states.length)) {
        newIndex = 0
      }

      if (component.states[index]) {
        component.states[index].selected = false
      }
      component.states[newIndex].selected = true

      return {
        component: component
      }
    })
  }

  back() {
    this.setState((prevState) => {
      let component = _.cloneDeep(prevState.component)
      let index = _.findIndex(component.states, {selected: true})
      let newIndex = (index === -1) ? 0 : (index - 1)

      if (newIndex === -1) {
        newIndex = (component.states.length - 1)
      }

      if (component.states[index]) {
        component.states[index].selected = false
      }
      component.states[newIndex].selected = true

      return {
        component: component
      }
    })
  }

  render() {
    let component = this.state.component

    return (
      <div className="Preview">
        <div className="side-panel">
          <ul className="dimension-menu">
            {component.dimensions.map((dim) => {
              let selectedCtx = _.find(component.contexts, {id: this.state.contexts[dim.id] || ''}) || {}
              let contexts = _.filter(component.contexts, {dimension_id: dim.id})
              let open = dim.id === this.state.openId

              return (
                <li key={dim.id} className="dimension-menu-item">
                  <h5>{dim.title}</h5>
                  <div className={`dimension-dropdown ${open ? 'open' : ''}`}>
                    <h4 className="dimension-dropdown-title" onClick={(e) => this.toggle(e, dim.id)}>
                      {selectedCtx.title || 'Default (None)'}
                      <i className={`fa fa-chevron-${open ? 'up' : 'down'}`}></i>
                    </h4>

                    <ul className="dropdown-menu">
                      <li className="dropdown-menu-item" onClick={(e) => this.selectContext(e, dim.id, null)}>Default (None)</li>
                      {contexts.map((ctx) => {
                        return (
                          <li
                            key={ctx.id}
                            className={`dropdown-menu-item ${ctx.id === selectedCtx.id ? 'selected' : ''}`}
                            onClick={(e) => this.selectContext(e, dim.id, ctx.id)}>
                            {ctx.title}
                          </li>
                        )
                      })}
                    </ul>
                  </div>
                </li>
              )
            })}
          </ul>
        </div>

        <div className="main-panel">
          {/* <div className="state-view-selector">
            <i className={`fa fa-chevron-left state-view-dir ${!component.states.length ? 'disabled' : ''}`} onClick={() => this.back()}></i>
            <ul className="state-view-menu">
              {component.states.map((state) => {
                return (
                  <li onClick={() => this.select(state.id)} key={state.id} className={`state-view-item ${state.selected ? 'active' : ''}`}>{state.title}</li>
                )
              })}
            </ul>
            <i className={`fa fa-chevron-right state-view-dir ${!component.states.length ? 'disabled' : ''}`} onClick={() => this.forward()}></i>
          </div> */}
          <Canvas components={this.props.components} component={component}></Canvas>
        </div>
      </div>
    )
  }
}

export default Preview
