
import React from 'react'
import './ElementSelector.css'
import _ from 'lodash'

class ElementSelector extends React.Component {

  constructor(props) {
    super(props)

    let component = _.cloneDeep(props.component)
    if (component.elements.length && !_.find(component.elements, {selected: true})) {
      component.elements[0].selected = true
      props.update({elements: component.elements})
    }

    this.state = {
      open: false
    }
  }

  updateElements(elements) {
    this.props.update({elements: elements})
  }

  selectElement(element) {
    let elements = _.cloneDeep(this.props.component.elements)
    let bool = !element.selected

    _.forEach(elements, (el) => {
      el.selected = el.id === element.id ? bool : false
    })

    this.updateElements(elements)
  }

  dragOver(e, element) {
    e.stopPropagation()

    this.updateElements(_.map(this.props.component.elements, (el) => {
      el.targeted = el.id === element.id
      return el
    }))
  }

  enterHighlight(element) {
    let component = _.find(this.props.components, {id: element.component_id})
    if (component) {
      element = _.first(component.elements)
    }

    this.props.highlight(element.id)
  }

  renderElement(elements, element) {
    element = element || elements[0]
    let isRoot = _.findIndex(elements, element) === 0
    let children = _.filter(elements, {parent: element.id})

    let childrenElems

    if (children.length) {
      childrenElems = _.map(children, (childElem) => {
        return this.renderElement(elements, childElem)
      })
    }

    let hasComponent = !!element.component_id

    return (
      <div
        key={element.id}
        className={`selector-item
          ${hasComponent ? 'component-item' : ''}
          ${childrenElems ? 'has-children' : ''}
          ${isRoot ? 'root' : ''}
          ${element.selected ? 'selected' : ''}
        `}>
        <h3
          className={`selector-title ${element.targeted ? 'targeted' : ''}`}
          onDragOver={(e) => this.dragOver(e, element)}
          onClick={() => this.selectElement(element)}
          onMouseEnter={() => this.enterHighlight(element)}
          onMouseLeave={() => this.props.highlight(null)}
          >
          {element.title}
        </h3>
        {childrenElems}
      </div>
    )
  }

  render() {
    return (
      <div className="ElementSelector">
        {(() => {
          if (!this.props.component.elements.length) {
            return null
          }

          return this.renderElement(this.props.component.elements)
        })()}
      </div>
    )
  }

}

export default ElementSelector
