
import _ from 'lodash'
import util from './util'

export default {

  init() {
    let style = document.createElement('style')
    style.id = util.hashString()
    this.sheet = document.head.appendChild(style).sheet

    return style.id
  },

  destroy(id) {
    let style = document.querySelector(`#${id}`)

    if (style) {
      style.parentNode.removeChild(style)
    }
  },

  clear() {
    _.map(this.sheet.cssRules, (rule, index) => {
      if (this.sheet.cssRules[index]) {
        this.sheet.deleteRule(index)
      }
    })
  },

  render(component) {
    let results = []

    component.swatches.map((swatch) => {
      results.push(this.loadSwatch(swatch, component.styles))
    })

    results.map((result) => {
      this.css(result.selector, result.styleStr)
    })
  },

  loadSwatch(swatch, styles) {
    let s = _.filter(styles, {swatch_id: swatch.id})

    let selector = `.swatch-${swatch.id}`
    let styleStr = ''

    _.map(s, (style) => {
      if (!style.hidden) {
        styleStr += `${this.loadStyle(style)} `
      }
    })

    return {
      selector: selector,
      styleStr: styleStr
    }
  },

  loadStyle(style) {
    return `${style.domKey.replace( /([a-z])([A-Z])/g, '$1-$2' ).toLowerCase()}: ${style.domValue};`
  },

  css(selector, styleStr) {
    const rule = `${selector} { ${styleStr} }`
    const index = this.sheet.cssRules.length
    this.sheet.insertRule(rule, index)
  }

}
