
import React from 'react'
import './OrientationGraphic.css'

class OrientationGraphic extends React.Component {
  constructor(props) {
    super(props)

    this.state = {}
  }

  selectDir(e, dir) {
    e.stopPropagation()

    this.props.select(dir)
  }

  render() {
    let style = {
      top: `${this.props.y}%`,
      left: `${this.props.x}%`
    }

    return (
      <div className="OrientationGraphic" onClick={(e) => this.props.close(e)}>
        <div className="graphic-wrapper" style={style}>
          <svg xmlns="http://www.w3.org/2000/svg" viewBox="-1 -2 179 179">
            <path onClick={(e) => this.selectDir(e, 'north')} className="cls-1" d="M98.53,64.49a26.8,26.8,0,0,0-16.84-.07L58.54,7.82a87.68,87.68,0,0,1,63.64.28Z" transform="translate(-1.95 -1.98)" />
            <path onClick={(e) => this.selectDir(e, 'northEast')} className="cls-2" d="M114.07,78a26.66,26.66,0,0,0-11.85-12L125.87,9.64a87.34,87.34,0,0,1,44.8,45.2Z" transform="translate(-1.95 -1.98)" />
            <path onClick={(e) => this.selectDir(e, 'east')} className="cls-3" d="M115.51,98.53a26.66,26.66,0,0,0,.07-16.84l56.6-23.16a87.34,87.34,0,0,1-.28,63.64Z" transform="translate(-1.95 -1.98)" />
            <path onClick={(e) => this.selectDir(e, 'southEast')} className="cls-4" d="M102,114.07a26.66,26.66,0,0,0,12-11.85l56.39,23.65a87.35,87.35,0,0,1-45.2,44.8Z" transform="translate(-1.95 -1.98)" />
            <path onClick={(e) => this.selectDir(e, 'south')} className="cls-5" d="M90,178a87.54,87.54,0,0,1-32.21-6.12l23.65-56.39a26.8,26.8,0,0,0,16.84.07l23.15,56.6A87.39,87.39,0,0,1,90,178Z" transform="translate(-1.95 -1.98)" />
            <path onClick={(e) => this.selectDir(e, 'southWest')} className="cls-6" d="M54.13,170.36a87.35,87.35,0,0,1-44.8-45.2L65.93,102a26.66,26.66,0,0,0,11.85,12Z" transform="translate(-1.95 -1.98)" />
            <path onClick={(e) => this.selectDir(e, 'west')} className="cls-7" d="M7.82,121.46a87.35,87.35,0,0,1,.28-63.64L64.49,81.47a26.66,26.66,0,0,0-.07,16.84Z" transform="translate(-1.95 -1.98)" />
            <path onClick={(e) => this.selectDir(e, 'northWest')} className="cls-8" d="M9.64,54.13a87.36,87.36,0,0,1,45.2-44.8L78,65.93A26.67,26.67,0,0,0,66,77.78Z" transform="translate(-1.95 -1.98)" />
            <path onClick={(e) => this.selectDir(e, 'center')} className="cls-9" d="M90,112.91a22.92,22.92,0,1,1,8.66-1.71A22.8,22.8,0,0,1,90,112.91Z" transform="translate(-1.95 -1.98)" />
          </svg>
        </div>
      </div>
    )
  }
}

export default OrientationGraphic
