
import React from 'react'
import './ElementBuilder.css'
import _ from 'lodash'
import util from '../../services/util'

class ElementBuilder extends React.Component {

  constructor(props) {
    super(props)

    this.dragged = _.throttle(this.dragOver, 100)

    this.state = {
      filteredComponents: [],
      dragTarget: null,
      dropTarget: null
    }
  }

  update(elements) {
    this.props.update({elements: elements})
  }

  addElement(id) {
    let elements = _.cloneDeep(this.props.component.elements)
    let newElement = {
      id: `el_${util.hashString()}`,
      title: 'Untitled Element',
      component_id: null,
      parent: id
    }

    if (!elements.length) {
      this.update([newElement])
    }

    let index = _.findIndex(elements, {id: id})
    elements.splice(index + 1, 0, newElement)

    this.update(elements)
  }

  removeElement(id) {
    let elements = _.cloneDeep(this.props.component.elements)

    let result = _.filter(elements, (element) => {
      return element.id !== id && element.parent !== id
    })

    this.update(result)
  }

  selectComponent(element, component) {
    let elements = _.cloneDeep(this.props.component.elements)

    this.update(_.map(elements, (el) => {
      if (el.id === element.id) {
        el.title = component.title
        el.component_id = component.id
      }
      return el
    }))

    this.setState({openId: false, filteredComponents: []})
  }

  editTitle(id, title) {
    let elements = _.cloneDeep(this.props.component.elements)
    let index = _.findIndex(elements, {id: id})

    elements[index].title = title

    this.update(elements)

    let filteredComponents = this.props.components

    if (title) {
      filteredComponents = _.filter(this.props.components, (component) => {
        let regex = new RegExp(title, 'gi')
        return regex.test(component.title)
      })
    }

    if (index === 0) {
      filteredComponents = []
    }

    this.setState({
      filteredComponents: filteredComponents,
      openId: filteredComponents.length ? id : null
    })
  }

  dragStart(id) {
    this.setState({dragTarget: id})
  }

  dragEnd() {
    this.setState((prevState) => {
      if (prevState.dragTarget === prevState.dropTarget) {
        return null
      }

      let elements = _.cloneDeep(this.props.component.elements)
      let dragTargetElement = elements.splice(_.findIndex(elements, {id: prevState.dragTarget}), 1)[0]
      let dropIndex = _.findIndex(elements, {id: prevState.dropTarget})
      let dropTargetElement = _.find(elements, {id: prevState.dropTarget})

      if (prevState.nest) {
        dragTargetElement.parent = prevState.dropTarget
      } else {
        dragTargetElement.parent = dropTargetElement.parent || prevState.dropTarget
      }

      elements.splice(dropIndex + 1, 0, dragTargetElement)

      this.update(elements)

      return {
        dropTarget: null,
        nest: false
      }
    })
  }

  dragOver(id, nested) {
    this.setState({
      dropTarget: id,
      nest: !!nested
    })
  }

  renderElement(elements, element) {
    element = element || elements[0]
    let index = _.findIndex(elements, element)
    let isRoot = index === 0
    let children = _.filter(elements, {parent: element.id})

    let childrenElems

    if (children.length) {
      childrenElems = _.map(children, (childElem) => {
        return this.renderElement(elements, childElem)
      })
    }

    return (
      <div
        key={element.id}
        className={`element-item
          ${element.component_id ? 'component' : ''}
          ${this.state.openId === element.id ? 'open' : ''}
          ${childrenElems ? 'has-children' : ''}
          ${isRoot ? 'root' : ''}
          ${this.state.dropTarget === element.id && this.state.nest ? 'dragged' : ''}
        `}>
        <span
          onDragEnd={() => this.dragEnd()}
          onDragStart={() => this.dragStart(element.id)}
          onDragOver={() => this.dragOver(element.id, true)}
          draggable={!isRoot}
          className="element-item-label">

          <span className="element-input-wrapper">
            <input
              autoFocus={index === 0}
              spellCheck={false}
              placeholder="Enter a name"
              className="element-item-input"
              type="text"
              value={element.title}
              onBlur={() => this.setState({openId: null, filteredComponents: []})}
              onChange={(e) => this.editTitle(element.id, e.target.value)}
            />

            <ul className="element-component-menu">
              {this.props.components.map((component) => {
                return (
                  <li className="element-component-item" key={component.id} onMouseDown={() => this.selectComponent(element, component)}>{component.title}</li>
                )
              })}
            </ul>
          </span>

          <span>
            {(() => {
              if (element.component_id) {
                return null
              }
              return (
                <i className="fa fa-plus element-item-icon" onClick={() => this.addElement(element.id)}></i>
              )
            })()}
            <i className="fa fa-minus element-item-icon" onClick={() => this.removeElement(element.id)}></i>
          </span>
        </span>
        <span
          className={`element-item-divider ${this.state.dropTarget === element.id && !this.state.nest ? 'dragged' : ''}`}
          onDragOver={() => this.dragOver(element.id, false)}>
        </span>
        {childrenElems}
      </div>
    )
  }

  render() {
    let elements = this.props.component.elements

    return (
      <div className="Builder">
        <p className="element-cta">Scaffold your component by adding elements</p>

        <div className="elements-list">
          {(() => {
            if (!elements.length) {
              return (
                <button className="add-element" onClick={() => this.addElement()}>Add an Element</button>
              )
            }

            return this.renderElement(elements)
          })()}
        </div>

      </div>
    )
  }

}

export default ElementBuilder
