
import React from 'react'
import './NavTab.css'
import _ from 'lodash'
import ElementBuilder from '../ElementBuilder'
import ElementSelector from '../ElementSelector'
import EditingCanvas from '../EditingCanvas'
import SwatchBuilder from '../SwatchBuilder'
import Save from '../Save'
import ContextBuilder from '../ContextBuilder'
import ContextSelector from '../ContextSelector'
import FlowBuilder from '../FlowBuilder'
import Preview from '../Preview'
import event from '../../services/event'
import InfoConfirm from '../InfoConfirm'

class NavTab extends React.Component {

  constructor(props) {
    super(props)

    this.saved = true

    this.state = {
      highlightId: null,
      component: props.component,
      tab: 'elements',
      tabs: [
        {
          id: 'elements',
          title: 'Elements',
          tag: 'Build'
        },
        {
          id: 'context',
          title: 'Context',
          tag: 'Build'
        },
        {
          id: 'style',
          title: 'Style',
          tag: 'Design'
        },
        {
          id: 'flow',
          title: 'Flow',
          tag: 'Design'
        },
        {
          id: 'inspect',
          title: 'Inspect',
          tag: 'View'
        },
        {
          id: 'preview',
          title: 'Preview',
          tag: 'View'
        }
      ]
    }
  }

  dragOver() {
    this.setState((prevState) => {
      let component = _.cloneDeep(prevState.component)

      component.elements = _.map(component.elements, (element) => {
        element.targeted = false
        return element
      })

      return {
        component: component
      }
    })
  }

  updateTitle(title) {
    this.setState((prevState) => {
      let component = _.cloneDeep(prevState.component)

      component.title = title

      return {
        component: component
      }
    })
  }

  render_elements() {
    return (
      <div className="build">
        <ElementBuilder components={this.props.components} component={this.state.component} update={(component) => this.updateComponent(component)}></ElementBuilder>
      </div>
    )
  }

  render_context() {
    return (
      <div className="context">
        <ContextBuilder component={this.state.component} update={(component) => this.updateComponent(component)}></ContextBuilder>
      </div>
    )
  }

  highlight(id) {
    this.setState({highlightId: id})
  }

  render_style() {
    return (
      <div className="style">
        <div className="style-section">
          <h4 className="subtitle">Element</h4>
          <ElementSelector components={this.props.components} highlight={(id) => this.highlight(id)} update={(component) => this.updateComponent(component)} component={this.state.component}></ElementSelector>
        </div>
        <div className="style-section">
          <h4 className="subtitle">Context</h4>
          <ContextSelector update={(component) => this.updateComponent(component)} component={this.state.component}></ContextSelector>
        </div>
        <div className="style-section expand">
          <h4 className="subtitle">Canvas</h4>
          <EditingCanvas highlightId={this.state.highlightId} component={this.state.component} components={this.props.components}></EditingCanvas>
        </div>
        <div className="style-section">
          <h4 className="subtitle">Swatch</h4>
          <SwatchBuilder update={(component) => this.updateComponent(component)} filterContext={false} component={this.state.component}></SwatchBuilder>
        </div>
      </div>
    )
  }

  render_flow() {
    return (
      <div className="flow">
        <FlowBuilder component={this.state.component}></FlowBuilder>
      </div>
    )
  }

  render_inspect() {
    return (
      <div className="inspect">
        <p>Coming soon</p>
      </div>
    )
  }

  render_preview() {
    return (
      <div className="preview">
        <Preview components={this.props.components} component={this.state.component}></Preview>
      </div>
    )
  }

  updateComponent(component) {
    this.setState((prevState) => {
      let prevComponent = prevState.component

      _.merge(prevComponent, component)

      _.forIn(component, (v, k) => {
        prevComponent[k] = v
      })

      return {
        component: prevComponent
      }
    })
  }

  selectTab(tabId) {
    this.setState({tab: tabId})
  }

  home() {
    if (this.saved) {
      return this.props.exit()
    }

    event.trigger('modal', () => {
      return (
        <InfoConfirm
          message="You have unsaved progress. Are you sure you want to leave?"
          yes="Yes"
          no="No"
          confirm={(bool) => {
            event.trigger('modal', null)

            if (bool) {
              this.props.exit()
            }
          }}>
        </InfoConfirm>
      )
    })
  }

  render() {
    return (
      <div className="NavTab" onDragOver={() => this.dragOver()}>
        <div className="tab-header">
          <h1 className="tab-header-title">
            <i onClick={() => this.home()} className="fa fa-home tab-header-title-icon"></i>
          </h1>

          <div className="meta-info">
            <input placeholder="Enter a name" type="text" value={this.state.component.title} onChange={(e) => this.updateTitle(e.target.value)} />
            <Save saved={(bool) => this.saved = bool} component={this.state.component}></Save>
          </div>

          <ul className="tab-header-list">
            {this.state.tabs.map((tab) => {
              return (
                <li
                  key={`t_${tab.id}`}
                  onClick={() => this.selectTab(tab.id)}
                  className={`tab-header-item ${tab.id === this.state.tab ? 'active' : ''}`}>
                  <small className={`tab-header-sub-title ${tab.tag}`}>{tab.tag}</small>
                  {tab.id === 'setup' && this.state.component.title ? this.state.component.title : tab.title}
                </li>
              )
            })}
          </ul>
        </div>

        <div className="tab-body">
          {this[`render_${this.state.tab}`]()}
        </div>
      </div>
    )
  }

}

export default NavTab
