
import React from 'react'
import './InfoConfirm.css'

export default (props) => {
  let infoType = props.infoType || 'default'

  return (
    <div className={`InfoConfirm ${infoType}`}>
      <p>{props.message}</p>

      <button onClick={() => props.confirm(true)} className="confirm">{props.yes || 'Ok'}</button>
      <button onClick={() => props.confirm(false)} className="deny">{props.no || 'Cancel'}</button>
    </div>
  )
}
