
import React from 'react'
import './Modal.css'
import event from '../../services/event'

class Modal extends React.Component {

  constructor(props) {
    super(props)

    event.on('modal', (renderFn) => {
      this.setState({renderFn: renderFn})
    })

    this.state = {
      renderFn: null
    }
  }

  render() {
    return (
      <div className={`Modal ${this.state.renderFn ? 'open' : ''}`}>
        <div className="modal-content">
          {this.state.renderFn ? this.state.renderFn() : null}
        </div>
      </div>
    )
  }

}

export default Modal
