
import React from 'react'
import './Setup.css'
import _ from 'lodash'
import util from '../../services/util'
import tutorials from '../../services/tutorials'
import templates from '../../services/template'
import model from '../../services/model'
import event from '../../services/event'
import InfoConfirm from '../InfoConfirm'

class Setup extends React.Component {

  constructor(props) {
    super(props)

    let components = window.localStorage.getItem('components')
    components = components ? JSON.parse(components) : []

    this.listenerId = util.hashString()

    event.on('appClick', (e) => {
      this.setState((prevState) => {
        let state = {}

        if (prevState.showTutorial) {
          state.showTutorial = false
        }

        if (prevState.showLoad) {
          state.showLoad = false
        }

        if (prevState.showTemplate) {
          state.showTemplate = false
        }

        return state
      })
    }, this.listenerId)

    let dimension_id = util.hashString()

    this.state = {
      template: null,
      templates: templates,
      tutorials: tutorials,
      showTutorial: false,
      showLoad: false,
      showTemplate: false,
      components: components,
      component: {
        id: util.hashString(),
        title: '',
        elements: [
          {
            id: util.hashString(),
            title: 'Container'
          }
        ],
        dimensions: [
          {
            id: dimension_id,
            title: 'Dimension 1'
          }
        ],
        contexts: [
          {
            id: util.hashString(),
            title: 'Context 1',
            dimension_id: dimension_id
          }
        ],
        swatches: [],
        styles: [],
        states: [],
        flows: []
      }
    }
  }

  componentWillUnmount() {
    event.off('appClick', this.listenerId)
  }

  toggleDropdown(e, prop) {
    e.stopPropagation()

    this.setState((prevState) => {
      let state = {}
      state[prop] = !prevState[prop]

      return state
    })
  }

  updateTitle(title) {
    this.setState((prevState) => {
      let component = _.cloneDeep(prevState.component)
      component.title = title

      return {
        component: component
      }
    })
  }

  selectTemplate(template) {
    this.setState({template: template})
  }

  loadComponent(component) {
    this.props.begin(component)
  }

  removeComponent(e, component) {
    e.stopPropagation()

    event.trigger('modal', () => {
      return (
        <InfoConfirm
          message="Are you sure you want to delete this component?"
          yes="Yes"
          no="No"
          confirm={(bool) => {
            event.trigger('modal', null)

            if (bool) {
              this.setState({
                components: model.removeComponent(component.id)
              })
            }
          }}>
        </InfoConfirm>
      )
    })
  }

  loadTutorial(tutorial) {
    event.trigger('modal', () => {
      return (
        <InfoConfirm
          message="Warning! Tutorials do not follow your actions. When finished with an instruction, you must manually click the forward button to proceed."
          confirm={(bool) => {
            event.trigger('modal', null)

            if (bool) {
              this.props.tutorial(tutorial)
              let component = this.state.component
              component.title = ''
              this.props.begin(this.state.component)
            }
          }}
        ></InfoConfirm>
      )
    })
  }

  create() {
    let component = _.cloneDeep(this.state.template || this.state.component)
    component.title = this.state.component.title

    this.props.begin(component)
  }

  render() {
    return (
      <div className="Setup">
        <p className="intro">Welcome. To get started, choose one of the options below.</p>

        <div className="setup-body">
          <div className="setup-section">
            <h3 className="section-title">
              New
              <small>create a new component</small>
            </h3>

            <input autoComplete="off" placeholder="Enter a name" className="name-input" type="text" id="c_title" value={this.state.component.title} onChange={(e) => this.updateTitle(e.target.value)} />

            <div className="component-template">
              <label>(optional) select a template</label>

              <button className={`setup-button load
                ${this.state.showTemplate ? 'open' : ''}
                ${this.state.templates.length ? '' : 'disabled'}
                `} onClick={(e) => this.toggleDropdown(e, 'showTemplate')}>
                {this.state.template ? this.state.template.title : 'None'}
                <i className="fa fa-chevron-down"></i>

                <ul className="load-options">
                  <li className="load-option" onMouseDown={() => this.selectTemplate(null)}>None</li>
                  {this.state.templates.map((template) => {
                    return (
                      <li onMouseDown={() => this.selectTemplate(template)} className="load-option" key={template.id}>{template.title}</li>
                    )
                  })}
                </ul>
              </button>
            </div>

            <button onClick={() => this.create()} className={`setup-button create ${this.state.component.title ? '' : 'disabled'}`}>Create</button>
          </div>

          <div className="setup-section">
            <h3 className="section-title">
              Load
              <small>open a saved component</small>
            </h3>

            <button className={`setup-button load
              ${this.state.showLoad ? 'open' : ''}
              ${this.state.components.length ? '' : 'disabled'}
              `} onClick={(e) => this.toggleDropdown(e, 'showLoad')}>
              Select a component
              <i className="fa fa-chevron-down"></i>

              <ul className="load-options">
                {this.state.components.map((component) => {
                  return (
                    <li onMouseDown={() => this.loadComponent(component)} className="load-option" key={component.id}>
                      {component.title || 'Untitled Component'}
                      <i onMouseDown={(e) => this.removeComponent(e, component)} className="fa fa-times load-option-icon remove"></i>
                    </li>
                  )
                })}
              </ul>
            </button>
          </div>

          <div className="setup-section">
            <h3 className="section-title">
              Tutorial
              <small>take an interactive tour</small>
            </h3>

            <button className={`setup-button load
              ${this.state.showTutorial ? 'open' : ''}
              ${this.state.tutorials.length ? '' : 'disabled'}
              `} onClick={(e) => this.toggleDropdown(e, 'showTutorial')}>
              Select a tutorial
              <i className="fa fa-chevron-down"></i>

              <ul className="load-options">
                {this.state.tutorials.map((tutorial) => {
                  return (
                    <li onMouseDown={() => this.loadTutorial(tutorial)} className="load-option" key={tutorial.id}>{tutorial.title}</li>
                  )
                })}
              </ul>
            </button>
          </div>
        </div>
      </div>
    )
  }

}

export default Setup
