
import React from 'react'
import './ContextSelector.css'
import _ from 'lodash'
import Selector from '../Selector'
import event from '../../services/event'
import util from '../../services/util'

class ContextSelector extends React.Component {
  constructor(props) {
    super(props)

    let selectedContext = _.find(props.component.contexts, {selected: true})

    this.listenerId = util.hashString()

    event.on('appClick', () => {
      this.setState({open: false})
    }, this.listenerId)

    this.state = {
      open: false,
      selectedContext: selectedContext ? selectedContext.id : null
    }
  }

  componentWillUnmount() {
    event.off('appClick', this.listenerId)
  }

  selectContext(id) {
    let contexts = _.map(this.props.component.contexts, (ctx) => {
      ctx.selected = ctx.id === id ? !ctx.selected : false

      return ctx
    })

    this.props.update({contexts: contexts})

    this.setState({
      selectedContext: id,
      open: false
    })
  }

  render() {
    let contexts = this.props.component.contexts

    return (
      <div className="ContextSelector">
        <Selector
          nullValue="Default (None)"
          open={this.state.open}
          toggle={() => this.setState({open: !this.state.open})}
          select={(id) => this.selectContext(id)}
          options={contexts}>
        </Selector>
      </div>
    )
  }
}

export default ContextSelector
