import React from 'react'
import './App.css'
import Test from './components/Test'
import Modal from './components/Modal'
import _ from 'lodash'
import event from './services/event'

class App extends React.Component {

  constructor(props) {
    super(props)

    this.onDragHandlerId = event.on('beginDrag', () => {
      this.setState({dragging: true})
    })

    this.throttledMove = _.throttle(this.appMove, 25)

    this.state = {
      dragging: false
    }
  }

  appClick(e) {
    event.trigger('appClick', e)
  }

  appMove(rect) {
    if (!this.state.dragging) {
      return
    }

    event.trigger('appDrag', {
      rect: rect
    })
  }

  appRelease(e) {
    this.setState({dragging: false})

    event.trigger('appDragEnd')
  }

  appLeave(e) {
    this.setState({dragging: false})
  }

  render() {
    return (
      <div
        className="App"
        onClick={(e) => this.appClick(e)}
        onMouseUp={(e) => this.appRelease(e)}
        onMouseMove={(e) => this.throttledMove({x: e.clientX, y: e.clientY})}
        onMouseLeave={(e) => this.appLeave(e)}>
        <Test></Test>
        <Modal></Modal>
      </div>
    )
  }
}

export default App
