
import React from 'react'
import './Mock.css'
import _ from 'lodash'
import renderService from '../../services/render'

class Mock extends React.Component {

  constructor(props) {
    super(props)

    this.renderId = renderService.init(props.component)

    this.state = {}
  }

  componentWillUnmount() {
    renderService.destroy()
  }

  textContent(component, elementId, contexts) {
    let textContent = null
    let swatches = _.filter(component.swatches, {element_id: elementId, context_id: null})

    swatches.map((swatch) => {
      let textContentStyle = _.find(component.styles, {swatch_id: swatch.id, domKey: 'textContent'})

      if (textContentStyle) {
        textContent = textContentStyle.domValue
      }

      return null
    })

    contexts.map((ctx) => {
      let swatches = _.filter(component.swatches, {element_id: elementId, context_id: ctx.id})

      return swatches.map((swatch) => {
        let textContentStyle = _.find(component.styles, {swatch_id: swatch.id, domKey: 'textContent'})

        if (textContentStyle) {
          textContent = textContentStyle.domValue
        }

        return null
      })
    })

    return textContent
  }

  classNames(component, elementId, contexts) {
    let classNames = ''
    let swatches = _.filter(component.swatches, {element_id: elementId, context_id: null})

    swatches.map((swatch) => {
      classNames += ` swatch-${swatch.id}`
      return null
    })

    contexts.map((ctx) => {
      let swatches = _.filter(component.swatches, {element_id: elementId, context_id: ctx.id})

      return swatches.map((swatch) => {
        classNames += ` swatch-${swatch.id}`
        return null
      })
    })

    return classNames
  }

  renderElement(component, element, contexts) {
    element = element || component.elements[0]
    let index = _.findIndex(component.elements, {id: element.id})
    let children = _.filter(component.elements, {parent: element.id})

    let childrenElems

    if (children.length) {
      childrenElems = _.map(children, (childElem) => {
        return this.renderElement(component, childElem, contexts)
      })
    }

    if (element.component_id) {
      let component = _.find(this.props.components, {id: element.component_id})
      return this.renderComponent(component, null, null)
    }

    return (
      <div
        id={`el-${element.id}`}

        title={element.title}
        key={element.id}
        className={`Element
          ${index === 0 ? 'Component' : ''}
          ${this.classNames(component, element.id, contexts)}`}
        >
        {this.textContent(component, element.id, contexts)}
        {childrenElems}
      </div>
    )
  }

  renderComponent(component, contexts) {
    return this.renderElement(component, null, contexts)
  }

  render() {
    renderService.clear()

    if (!this.props.component || !this.props.component.elements.length) {
      return null
    }

    renderService.render(this.props.component)

    let contexts = _.filter(this.props.component.contexts, {selected: true})

    return (
      <div className="Mock">
        {this.renderComponent(this.props.component, contexts)}
      </div>
    )
  }

}

export default Mock
