
import React from 'react'
import './ContextBuilder.css'
import _ from 'lodash'
import util from '../../services/util'

class ContextBuilder extends React.Component {
  constructor(props) {
    super(props)

    this.state = {
      dragTarget: null,
      dropTarget: null
    }
  }

  addDimension() {
    let dimensions = this.props.component.dimensions

    dimensions.push(
      {
        id: util.hashString(),
        title: `Dimension ${dimensions.length + 1}`
      }
    )

    this.props.update({dimensions: dimensions})
  }

  removeDimension(id) {
    let context_ids = []

    let dimensions = _.filter(this.props.component.dimensions, (dim) => {
      return dim.id !== id
    })

    let contexts = _.filter(this.props.component.contexts, (ctx) => {
      if (ctx.dimension_id === id) {
        context_ids.push(ctx.id)
      }
      return ctx.dimension_id !== id
    })

    let swatches = _.filter(this.props.component.swatches, (swatch) => {
      return context_ids.indexOf(swatch.context_id) === -1
    })

    this.props.update({
      dimensions: dimensions,
      contexts: contexts,
      swatches: swatches
    })
  }

  addContext(dimension_id) {
    let contexts = this.props.component.contexts
    let dimTitle = _.find(this.props.component.dimensions, {id: dimension_id}).title
    let ctxDimLength = _.filter(contexts, {dimension_id: dimension_id}).length

    contexts.push(
      {
        id: util.hashString(),
        title: `${dimTitle} ${ctxDimLength + 1}`,
        dimension_id: dimension_id
      }
    )

    this.props.update({contexts: contexts})
  }

  removeContext(id) {
    let contexts = _.filter(this.props.component.contexts, (ctx) => {
      return ctx.id !== id
    })

    let swatches = _.filter(this.props.component.swatches, (swatch) => {
      return swatch.context_id !== id
    })

    this.props.update({
      contexts: contexts,
      swatches: swatches
    })
  }

  updateContextTitle(id, title) {
    let contexts = _.cloneDeep(this.props.component.contexts)
    let index = _.findIndex(contexts, {id: id})

    contexts[index].title = title

    this.props.update({
      contexts: contexts
    })
  }

  updateDimensionTitle(id, title) {
    let dimensions = _.cloneDeep(this.props.component.dimensions)
    let index = _.findIndex(dimensions, {id: id})

    dimensions[index].title = title

    this.props.update({
      dimensions: dimensions
    })
  }

  dragStart(id) {
    this.setState({dragTarget: id})
  }

  dragEnd() {
    this.setState((prevState) => {
      if (prevState.dragTarget === prevState.dropTarget) {
        return null
      }

      let contexts = _.cloneDeep(this.props.component.contexts)
      let dragTargetElement = contexts.splice(_.findIndex(contexts, {id: prevState.dragTarget}), 1)[0]
      let dropIndex = _.findIndex(contexts, {id: prevState.dropTarget})

      contexts.splice(dropIndex + 1, 0, dragTargetElement)

      this.props.update({
        contexts: contexts
      })

      return {
        dropTarget: null
      }
    })
  }

  dragOver(id) {
    this.setState({
      dropTarget: id
    })
  }

  renderContext(ctx, dimIndex, ctxIndex) {
    let autoFocus = dimIndex === 0 && ctxIndex === 0
    return (
      <div
        className="ctx"
        onDragEnd={() => this.dragEnd()}
        onDragStart={() => this.dragStart(ctx.id)}
        onDragOver={() => this.dragOver(ctx.id)}
        draggable={true}>
        <input
          autoFocus={autoFocus}
          className="ctx-input"
          type="text"
          value={ctx.title}
          onChange={(e) => this.updateContextTitle(ctx.id, e.target.value)}
        />
        <i className="fa fa-times ctx-remove" onClick={() => this.removeContext(ctx.id)}></i>
      </div>
    )
  }

  render() {
    let component = this.props.component

    if (!component) {
      return null
    }

    return (
      <div className="ContextBuilder">
        <p className="context-cta">Give options to your component by adding contexts</p>

        <ul className="dimension-menu">
          {component.dimensions.map((dim, i) => {
            return (
              <li key={dim.id} className="dimension-menu-item">
                {(() => {
                  let contexts = _.filter(component.contexts, {dimension_id: dim.id})

                  return (
                    <div className="ctx-wrapper">
                      <h3 className="ctx-wrapper-title">
                        <input
                          type="text"
                          className="ctx-wrapper-input"
                          onChange={(e) => this.updateDimensionTitle(dim.id, e.target.value)}
                          value={dim.title}
                        />
                        <i className="fa fa-times remove-dim" onClick={() => this.removeDimension(dim.id)}></i>
                      </h3>

                      <ul className="context-menu">
                        {contexts.map((ctx, n) => {
                          return (
                            <li className="context-menu-item" key={ctx.id}>
                              {this.renderContext(ctx, i, n)}
                              <div
                                className={`divider ${this.state.dropTarget === ctx.id ? 'dragged' : ''}`}
                                onDragOver={() => this.dragOver(ctx.id)}>
                              </div>
                            </li>
                          )
                        })}
                      </ul>

                      <button className="add-context" onClick={() => this.addContext(dim.id)}>
                        add a context
                      </button>
                    </div>
                  )
                })()}
              </li>
            )
          })}
          <li className="dimension-menu-item">
            <button className="add-dimension" onClick={() => this.addDimension()}>
              Add dimension
              <i className="fa fa-plus"></i>
            </button>
          </li>
        </ul>
      </div>
    )
  }
}

export default ContextBuilder
