
import React from 'react'
import './Guideline.css'

export default (props) => {
  let pctProp = props.axis === 'x' ? 'left' : 'top'
  let style = {}
  style[pctProp] = props.pct + '%'

  let down = false
  let mouseDown = (rect) => {
    down = true
  }
  let mouseMove = (moved) => {
    if (!down) {
      return
    }

    if (Math.abs(moved.x) || Math.abs(moved.y)) {
      down = false
      props.beginDrag()
    }
  }

  return (
    <div
      className={`Guideline ${props.axis}-axis ${props.showLine ? 'show-line' : ''}`}
      style={style}>
      <div className="line"></div>
      <button
        className={`handle ${props.showHandle ? 'show' : ''}`}
        onMouseDown={() => mouseDown()}
        onMouseMove={(e) => mouseMove({x: e.movementX, y: e.movementY})}
        onMouseUp={() => down = false}
        onDoubleClick={() => props.onReset()}
      >
        <i></i>
      </button>
    </div>
  )
}
