
import React from 'react'
import './Test.css'
import Setup from '../Setup'
import NavTab from '../NavTab'
import Tutorial from '../Tutorial'
import model from '../../services/model'
import _ from 'lodash'

class Test extends React.Component {
  constructor(props) {
    super(props)

    this.state = {
      tutorial: null,
      component: null,
      components: []
    }
  }

  loadTutorial(tutorial) {
    this.setState({tutorial: tutorial})
  }

  begin(component) {
    let components = model.components()
    this.setState({
      component: component,
      components: _.filter(components, (c) => {
        return c.id !== component.id
      })
    })
  }

  render() {
    return (
      <div className="Test">
        {(() => {
          if (this.state.tutorial) {
            return (
              <Tutorial tutorial={this.state.tutorial}></Tutorial>
            )
          }

          return null
        })()}

        {(() => {
          if (this.state.component) {
            return (
              <NavTab exit={() => this.setState({component: null})} components={this.state.components} component={this.state.component}></NavTab>
            )
          }

          return (
            <Setup tutorial={(tutorial) => this.loadTutorial(tutorial)} begin={(component) => this.begin(component)}></Setup>
          )
        })()}
      </div>
    )
  }
}

export default Test
