
import React from 'react'
import './Selector.css'
import _ from 'lodash'

class Selector extends React.Component {
  constructor(props) {
    super(props)

    this.state = {}
  }

  toggle(e) {
    e.stopPropagation()

    this.props.toggle()
  }

  select(e, id) {
    e.stopPropagation()

    this.props.select(id)
  }

  render() {
    let options = this.props.options
    let selected = _.find(options, {selected: true}) || {
      id: 0,
      title: 'None'
    }

    return (
      <div className={`Selector ${this.props.open ? 'open' : ''}`}>
        <h4 className="selector-title" onClick={(e) => this.toggle(e)}>
          {selected.title}
          <i className={`fa fa-chevron-${this.props.open ? 'up' : 'down'}`}></i>
        </h4>

        <ul className="selector-menu">
          <li className="selector-menu-item" onClick={(e) => this.select(e, null)}>{this.props.nullValue}</li>
          {options.map((opt) => {
            return (
              <li
                key={opt.id}
                className={`selector-menu-item ${opt.id === selected.id ? 'selected' : ''}`}
                onClick={(e) => this.select(e, opt.id)}>
                {opt.title}
              </li>
            )
          })}
        </ul>
      </div>
    )
  }
}

export default Selector
