
import React from 'react'
import './StyleEditor.css'
import _ from 'lodash'
import properties from '../../services/properties'

class StyleEditor extends React.Component {

  constructor(props) {
    super(props)

    this.availableProperties = properties

    this.state = {
      open: false,
      focused: false,
      delete: false,
      filteredProperties: this.availableProperties
    }
  }

  toggleStyle() {
    let style = _.cloneDeep(this.props.style)
    style.hidden = !style.hidden

    this.props.update(style)
  }

  componentDidMount() {
    if (this._keyRef) {
      this._keyRef.focus()
    }
  }

  findProperty(propInput) {
    let property
    property = property || _.find(this.availableProperties, {id: propInput})
    property = property || _.find(this.availableProperties, {title: propInput})
    property = property || _.find(this.availableProperties, {cssId: propInput})

    return property
  }

  updateKey(propInput, property) {
    let style = _.cloneDeep(this.props.style)
    let forward = false

    if (property) {
      forward = true
      style.domKey = property.cssId
      style.propInput = property.title
    } else {
      property = this.findProperty(propInput)
      style.domKey = property ? property.cssId : propInput
      style.propInput = propInput
    }

    this.props.update(style)

    this.setState((prevState) => {
      let regex = new RegExp(propInput, 'gi')

      return {
        filteredProperties: _.filter(this.availableProperties, (property) => {
          return regex.test(property.id) || regex.test(property.cssId) || regex.test(property.title)
        })
      }
    })

    if (forward && this._valueRef) {
      window.setTimeout(() => {
        this._valueRef.focus()
      }, 0)
    }
  }

  updateValue(value) {
    let style = _.cloneDeep(this.props.style)
    style.domValue = value

    this.props.update(style)
  }

  focus() {
    this.setState({
      focused: true
    })
  }

  blur() {
    this.setState({
      delete: false,
      focused: false
    })
  }

  propKeyDown(e) {
    if (e.keyCode === 27) {
      this.setState({focused: false})
    }

    let style = _.cloneDeep(this.props.style)

    if (e.keyCode === 8 && this.state.delete) {
      this.props.remove(style.id)
    }
  }

  propKeyUp(e) {
    let state = {}

    let style = _.cloneDeep(this.props.style)

    state.delete = e.keyCode === 8 && !style.domKey
    state.focused = e.keyCode !== 27 && e.keyCode !== 8

    this.setState(state)
  }

  render() {
    let style = this.props.style
    let properties = _.sortBy(this.state.filteredProperties, (property) => {
      return property.id
    })

    return (
      <div className={`StyleEditor
        ${this.state.delete ? 'delete' : ''}
        ${this.state.open ? 'open' : ''}
        ${style.hidden ? 'hidden' : ''}
      `}>
        <div className="style-body">
          <div className="style-input dom-key">
            <input
              ref={(e) => this._keyRef = e}
              className={this.state.focused ? 'focused' : ''}
              onKeyDown={(e) => this.propKeyDown(e)}
              onKeyUp={(e) => this.propKeyUp(e)}
              onBlur={() => this.blur()}
              onClick={() => this.focus()}
              placeholder="enter a style property"
              type="text"
              value={style.propInput}
              onChange={(e) => this.updateKey(e.target.value)}
            />

            <ul className="key-dropdown">
              {properties.map((property) => {
                return (
                  <li key={property.id} onMouseDown={() =>  this.updateKey(property.cssId, property)} className="key-item">{property.title}</li>
                )
              })}
            </ul>
          </div>
          <div className="style-input dom-value">
            <input
              ref={(e) => this._valueRef = e}
              placeholder="enter a style value"
              type="text"
              value={style.domValue}
              onChange={(e) => this.updateValue(e.target.value)}
            />
          </div>
          <div className="style-icon" onClick={() => this.setState({open: !this.state.open})}>
            <i className={`fa fa-chevron-${this.state.open ? 'up' : 'down'}`}></i>
          </div>
        </div>

        {/* <div className="style-footer">
          <div className="style-input dom-key">
            <label>Shadow X</label>
          </div>
          <div className="style-input dom-value">
            <input type="text" value="0px" />
          </div>

          <div className="style-input dom-key">
            <label>Shadow Y</label>
          </div>
          <div className="style-input dom-value">
            <input type="text" value="0px" />
          </div>

          <div className="style-input dom-key">
            <label>Blur</label>
          </div>
          <div className="style-input dom-value">
            <input type="text" value="1px" />
          </div>

          <div className="style-input dom-key">
            <label>Spread</label>
          </div>
          <div className="style-input dom-value">
            <input type="text" value="3px" />
          </div>

          <div className="style-input dom-key">
            <label>Color</label>
          </div>
          <div className="style-input dom-value">
            <input type="text" value="red" />
          </div>
        </div> */}
      </div>
    )
  }

}

export default StyleEditor
