
import React from 'react'
import './FlowBuilder.css'

class FlowBuilder extends React.Component {
  constructor(props) {
    super(props)

    this.state = {}
  }

  render() {
    let flows = this.props.component.flows || []

    return (
      <div className="FlowBuilder">

        <ul className="flow-menu">
          {flows.map((flow) => {
            return (
              <li key={flow.id} className="flow-menu-item">
                {flow.title}
              </li>
            )
          })}
        </ul>

      </div>
    )
  }
}

export default FlowBuilder
